    <div class="page-wrapper">
      <div class="content container-fluid">
        <el-skeleton v-if="loading" :rows="15" animated />
        <router-view v-else></router-view>
      </div>
    </div>
    <el-dialog
      v-model="logoutWarning"
      title="Session Timeout"
      width="30%"
      :before-close="close"
    >
      <p>
        You have been inactive for 14 minutes! Your session will expire if you
        remain inactive in the next 1 minute
      </p>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="danger" plain @click="Logout">Sign Out</el-button>
          <el-button type="primary" plain @click="resetTimers"
            >Continue</el-button
          >
        </span>
      </template>
    </el-dialog>
