</template>
<script>
export default {
  data() {
    return {
      data: "",
      loading: false,
      isActive: "Dashboard",
      isSubmenuActive: null,
      events: ["click", "scroll", "mousedown", "keypress", "load"], //"mousemove",
      warningTimer: null,
      logoutTimer: null,
      logoutWarning: false,
    };
  },
  computed: {
    user() {
      return this.$store.state.auth.user ? this.$store.state.auth.user : {};
    },
    notifications() {
      if (this.$store.state.account.notifications != undefined) {
        return this.$store.state.account.notifications;
      } else {
        return [];
      }
    },
    unread() {
      if (this.$store.state.account.notifications != undefined) {
        return this.$store.state.account.notifications.filter(function (row) {
          return row.status == 0;
        });
      } else {
        return [];
      }
    },
    read(data) {
      data.status = 1;
      this.$store.dispatch("account/notify", data).then(
        (res) => {
          this.company_branch = {};
          this.$store.dispatch("account/notifications");
          this.dialog = false;
          this.loading = false;
          this.$notify({
            title: "Success",
            message: res.data.message,
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
  },
  created() {
    var scripts = ["/assets/js/script.js"];
    scripts.forEach((script) => {
      let tag = document.createElement("script");
      tag.setAttribute("src", script);
      document.head.appendChild(tag);
    });
  },
  mounted() {
    this.$store.dispatch("account/notifications");
    this.events.forEach(function (event) {
      window.addEventListener(event, this.resetTimers);
    }, this);
    this.setTimers();
  },

  destroyed() {
    this.events.forEach(function (event) {
      window.removeEventListener(event, this.resetTimers);
    }, this);
    this.resetTimers();
  },
  methods: {
    setClass(active) {
      this.isActive = active;
    },
    setSubmenuClass(active) {
      this.isSubmenuActive = active;
    },
    Logout() {
      this.$store.dispatch("auth/logout");
      window.location.reload();
      this.$router.push("/login");
    },
    clearNotication(id) {
      if (confirm("Do you really want to clear all notifications?")) {
        this.$store
          .dispatch("account/delete")
          .then((response) => {
            this.$store.dispatch("account/notifications");
            this.$notify({
              title: "Success",
              message: response.message,
              type: "success",
            });
          })
          .catch((error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();

            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          });
      }
    },

    setTimers() {
      this.warningTimer = setTimeout(this.warningMessage, 14 * 60 * 1000); // Display warning in 14 Mins.
      this.logoutTimer = setTimeout(this.logoutUser, 15 * 60 * 1000); // Timeout in 15 mins
    },

    warningMessage() {
      this.logoutWarning = true;
    },

    logoutUser() {
      this.$notify({
        title: "Session Expiry",
        message:
          "Your session has expired. Please sign in again to continue using Techsavanna services",
        type: "warning",
        duration: 0,
      });
      this.Logout();
    },

    resetTimers() {
      this.logoutWarning = false;
      clearTimeout(this.warningTimer);
      clearTimeout(this.logoutTimer);
      this.setTimers();
    },
    close() {
      this.logoutWarning = true;
    },
  },
};
</script>